/* welcome section */

.welcomeContainer {
  position: relative;
  display: grid;
  height: 600px;
  grid-template-columns: 1fr 1fr;
}
.welcomeContainer::before {
  position: absolute;
  content : '';
  top: 0;left: 0;
  width : 100%;
  height: 100%;
  background-color: rgba(0,0,0,.5);
}
.welcomeRight {
  /* background-color: rgba(0,0,0); */
  background-image: url(/images/welcomeBg1.jpg);
}


.welcomeContentWrapper {
  /* padding : 5%; */
  position: absolute;
  top : 15%;
  left : 20%;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 65%;
  background-color: rgba(0,0,0,.7);
  padding : 60px 0px;
  border-radius: 10px;
}
.welcomeContentWrapper h1 {
  color : rgba(255,255,255,.9);
  /* font-family: cursive; */
  font-weight: 400;
}
.welcomeContentWrapper p {
  margin: 0px !important;
}
.welcomeContentWrapper .descriptionText {
  width: 80%;
  padding : auto;
}


/* quick menu section style */

.quickMenuContainer {
  margin : 100px 0px;
}

.quickMenuHeader {
  text-align: center;
  width: 50%;
  margin : auto;
  margin-bottom: 40px;
}
.quickMenuHeader span {
  /* color : var(--iconColor); */
  font-size: 70px;
}

.quickMenuItemsContainer {
  text-align: center;
  width: 85%;
  margin : auto;
  display: grid;
  gap: 30px;
  grid-template-columns: repeat(3, 1fr);
}
.quickMenuItemsContainer .QuickMenuItemBg {
  width: 100%;
  height: 400px;
  transition: 1.4s all ease;
}

/* presentation setion style */

.presentation {
  display: grid;
  grid-template-columns: 1.2fr 1fr 1fr;
  width: 80%;
  margin : auto;
  gap: 40px;
}
.presentationDescription .heading2 {
  font-size: 40px !important;
}
.presentation .presentationDescription p {
  font-size: 20px;
  padding-top: 20px;
}

/* video section style */

.videoBg{
  height: 100%;
  width: 100%;
  background-image: url(/images/food2.jpg);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color : whitesmoke;
  text-align: center;
  position: relative;
  margin-top: 50px;
}
.videoBg::before {
  position: absolute;
  content : '';
  top : 0;left : 0;
  width: 100%;
  height : 100%;
  background-color: rgba(0,0,0,.7);
}
.videoBg .vidDescription {
  position: relative;
  padding-bottom: 70px;
}
.vidDescription h1{
  font-size: 30px;
  /* font-family: cursive; */
  font-weight: 400 !important;
}
.vidDescription .icon-play2 {
  font-size: 50px !important;
  color : var(--iconColor);
  cursor: pointer;
}

.vidOverflow {
  position: fixed;
  top: 0;left: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(0,0,0,.7);
  z-index: 10;
  display: flex;
  justify-content: center;
  align-items: center;
}

.videoContainer {
  width: 75%;
  height: 75%;
}
.videoContainer video {
  width: 100%;
  height: 100%;
  outline-color: var(--iconColor);
}
.cancelBtn {
  position: absolute;
  top: 35px;right : 35px;
  font-size: 20px;
  font-weight: 400;
  transition: .5s all ease;
  cursor: pointer;
}

.cancelBtn:hover {
  color : var(--iconColor);
}

/* make a reservation section style */

.reservationsFormContainer {
  position: relative;
  background-image: url(/images/reserve.jpg);
  margin-top: 200px;
}
.reservationsFormContainer * {
  color : whitesmoke;
}
.reservationsFormContainer::before {
  position: absolute;
  content : '';
  top: 0;left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,.7);
}
.reservationsFormWrapper {
  width : 60%;
  margin : auto;
  padding : 100px 10px;
  position: relative;
}
.reservationHeader {
  width : 60%;
  margin : auto;
  text-align: center;
}
.reservationHeader h1 {
  margin : 0;
}
.reservationHeader span , .quickMenuHeader h1 span,
.presentationDescription h1 span, .contactHeader h1 span, 
.highlight {
  background-color: var(--iconColor);
  color : #333;
  padding : 2px;
  font-size: inherit;
}

form .formGroup{
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px 40px;
  margin-top: 60px;
  /* font-family: cursive; */
}
form label {
  display: block;
  margin-bottom: 5px;
}
form input, select {
  width: 100%;
  padding : 15px;
  outline-color: var(--iconColor);
  background-color: transparent;
  border: 1px solid grey;
  transition: .2s all ease;
}
select option{
 background-color: #333;
}
form input:focus, form select:focus{
  border-color: var(--iconColor);
  outline: none;
}
form button  {
/* i intentionally re-wrote the global button style (with lil changes of-course :)*/
outline : none;
border: none;
background-color : transparent;
font-size: 16px;
padding : 15px;
border : 1px solid rgba(255,165,0,.3);
}
form .centered {
  margin : 40px;
}

/* blog section style */

.blogsWrapper {
  padding: 10%;
  /* font-family: cursive; */
}
.blogsWrapper .centered {
  margin-bottom : 50px;
}
.blogsWrapper .centered .heading2 {
  position: relative;
  padding-top : 5px;
  font-size: 40px;
}
.blogsWrapper .centered .heading2::before {
  position: absolute;
  content: '';
  top: 0;left: 0;
  border-top: 3px solid var(--iconColor);
  height: 100%;
  width: 40%;
}
.blogs {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 30px;
}

.blogs .heading2 a {
  color : #333;
}
.blogs .heading2 a:hover {
  color : var(--iconColor);
}
.blogImg{
  width: 100%;
  transition: .5s all ease;
}
.blogImgCover {
  overflow: hidden;
}

.blogImg:hover {
  transform : scale(1.2);
  
}

.blogs ul {
  display: flex;
}
.blogs ul li {
  margin-right : 15px;
  color : rgb(179,179,179);
  font-size: 16px;
}
.blogs * a {
  color : var(--iconColor);
}
.blogs p > a {
  font-size: 18px;
}
.blogs .descriptionText2 {
  /* font-size: 18px; */
}

/* Insta Posts section style */

.instaPostsContainer {
  width: 90%;
  margin : auto;
  margin-bottom: 100px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap : 30px;
}
.instaHeader {
  text-align: center;
  margin-bottom: 50px;
}
.instaHeader p {
  width: 50%;
  margin : auto;
}
.grid-1, .grid-2 {
  display: grid;
  grid-template-rows: 1fr 1fr;
  row-gap: 20px;
}
.split {
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 20px;
}
.instaPostsWrapper .instaBg {
  position: relative;
  width: 100%;
  height: 350px;
  border-radius: 10px;
}

.iconOverlay {
  position: absolute;
  top : 0;
  display: none;
  justify-content: center; /*left to right*/
  align-items: center; /*top to buttom*/
  height: 100%;
  width: 100%;
  text-align: center;
  z-index: 2;
}
.instaBg:hover  .iconOverlay{
  animation: .5s colorEffect;
  display: flex;
  /* background-color: rgb(255,165,0,.3); */
  background-color: rgb(0,0,0,.6);
  border-radius: 10px;
}
.iconOverlay .iconFlat {
  border : 2px dotted whitesmoke;
  border-radius: 50%;
  padding : 7%;
  background: transparent;
  color : var(--iconColor);
}
.split .iconFlat {
  font-size: 25px;
  padding : 14%;
}

@keyframes colorEffect {
  from {opacity: .3};
  to {opacity: 1}
}

/* contact us section styles */


.contactContainer {
  width: 85%;
  margin : auto;
  margin-bottom: 100px;
}

.contactHeader {
  text-align: center;
  width: 50%;
  margin : auto;
  margin-bottom: 50px;
}

.contactHeader h1::first-letter {
  border-bottom: 3px solid var(--iconColor);
 }
 
.contactImage {
  width: 100%;
  height: 60%;
  background-image: url(/images/food2.jpg);
  border-radius: 10px;
}

.formGrid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap : 40px;
}

.qcl {
  margin-top: 30px;
}
.qcl .descriptionText2 {
  font-size: 18px;
}
.formGrid .quickContactLinks .qcl div * {
  display: inline;
}
.formGrid .quickContactLinks .qcl div {
  margin-top: 30px;
}
.formGrid div div .icon {
  font-size: 18px;
  margin-right: 20px;
  border : 1px solid var(--shade);
  padding : 12px;
}
.formGrid input {
  margin-bottom: 10px;
}
.formGrid .contactForm .centered button{
  display: block;
}
.formGrid button {
  width: 100%;
  color : whitesmoke;
  background-color: var(--iconColor);
  margin-top: 20px;
}
form textarea {
  width: 100%;
  min-height: 170px;
  max-height: 210px;
  margin-top: 20px;
  resize: vertical;
  outline-color: var(--iconColor);
  padding: 10px;
}
.form .contactForm label {
  margin-bottom: 20px;
}

.formGrid input::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: grey;
  opacity: 1; /* Firefox */
  /* font-family: cursive; */
}

.formGrid input:-ms-input-placeholder { /* Internet Explorer 10-11 */
  color: grey;
  /* font-family: cursive; */
}

.formGrid input::-ms-input-placeholder { /* Microsoft Edge */
  color: grey;
  /* font-family: cursive; */
}


/* Meet our employees style section */

.meetContentWrapper {
  width : 80%;
  margin : auto;
  margin-bottom: 100px;
}
.meetHeader {
  margin-bottom: 50px;
}
.meetHeader .styledHeader{
  color : var(--iconColor);
  font-weight: 400;
  margin-bottom: 0px;
}
.meetHeader .heading2 {
  margin-top: -20px;
}

.employeeContentWrapper {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 30px;
}
.employeeContentWrapper .staffImage {
  position: relative;
  height: 250px;
  width: 100%;
  overflow: hidden;
}
.employeeContainer .staffLinks {
  position: absolute;
  top: 0; bottom: 0;
  background-color: #333;
  transition: .5s all ease;
}

.employeeContainer .staffLinks ul {
  padding-top: 20px;
}
.employeeContainer .staffLinks ul li {
  padding : 10px;
}
.employeeContentWrapper .icon {
  color : whitesmoke;
}
.employeeContentWrapper .icon:hover {
  color : var(--iconColor);
}

.staffIdentity{
  display: flex;
  justify-content: space-between;
}